import { mapState, mapActions } from 'vuex';
const token = uni.getStorageSync('TOKEN');
export default {
	data() {
		return {
			show: false,
			info: null
		};
	},
	computed: {
		...mapState(['isLogin', 'userInfo'])
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad() {
		
	},
	onShow() {},
	methods: {
		...mapActions(['navFilter']),
		showShare() {
			uni.hideTabBar({});
			this.show = true;
		},
		closePopup() {
			this.show = false;
			uni.showTabBar({});
		},
		clear() {},
		async doShare(type) {
			let path='/pages/index/index'
			if(this.userInfo){
				path='/pages/index/index?server=' + this.userInfo.server_people_id
			}
			uni.share({
				provider: 'weixin',
				scene: type, //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏
				type: 5, //0-图文；5-小程序
				imageUrl:'../../static/poster.png',
				title: '欢迎使用易建联APP',
				miniProgram: {
					id:'gh_4bf03e4d36fb',
					path:path,
					webUrl:'https://uniapp.dcloud.io',
					type:2
				},
				success: e => {
					console.log('success', e);
					uni.showModal({
						content: '分享成功',
						showCancel: false
					});
				},
				fail: e => {
					console.log('fail', e);
					// uni.showModal({
					// 	content: e.errMsg,
					// 	showCancel:false
					// })
				},
				complete: function() {
					console.log('分享操作结束!');
				}
			});
			this.show = false;
			uni.showTabBar({});
		},
		compress() {
			//压缩图片 图文分享要求分享图片大小不能超过20Kb
			console.log('开始压缩');
			let img = '../../static/daily.png';
			return new Promise(res => {
				var localPath = plus.io.convertAbsoluteFileSystem(img.replace('file://', ''));
				console.log('after' + localPath);
				// 压缩size
				plus.io.resolveLocalFileSystemURL(
					localPath,
					entry => {
						entry.file(file => {
							// 可通过entry对象操作图片
							console.log('getFile:' + JSON.stringify(file));
							if (file.size > 20480) {
								// 压缩后size 大于20Kb
								plus.zip.compressImage(
									{
										src: img,
										dst: img.replace('.jpg', '2222.jpg').replace('.JPG', '2222.JPG'),
										width: '10%',
										height: '10%',
										quality: 1,
										overwrite: true
									},
									event => {
										console.log('success zip****' + event.size);
										let newImg = img.replace('.jpg', '2222.jpg').replace('.JPG', '2222.JPG');
										res(newImg);
									},
									function(error) {
										uni.showModal({
											content: '分享图片太大,需要请重新选择图片!',
											showCancel: false
										});
									}
								);
							}
						});
					},
					e => {
						console.log('Resolve file URL failed: ' + e.message);
						uni.showModal({
							content: '分享图片太大,需要请重新选择图片!',
							showCancel: false
						});
					}
				);
			});
		}
	}
};
